export const SET_SCALE = 'SET_SCALE';
export function setScale(value = null) {
  return {
    type: SET_SCALE,
    value
  }
}

export const TOGGLE_LAYER = 'TOGGLE_LAYER';
export function toggleLayer(index = null) {
  return {
    type: TOGGLE_LAYER,
    index
  }
}

export const UPDATE_STATUS_TICK = 'UPDATE_STATUS_TICK';
export function updateStatus() {
  return {
    type: UPDATE_STATUS_TICK
  }
}

export const UPDATE_STATUS_VALUE = 'UPDATE_STATUS_VALUE';
export function statusValueChange(id = null, value = null) {
  return {
    type: UPDATE_STATUS_VALUE,
    id,
    value
  }
}

export const UPDATE_STATUS_MAX_DATA_POINTS = 'UPDATE_STATUS_MAX_DATA_POINTS';
export function updateMaxDataPoints() {
  return {
    type: UPDATE_STATUS_MAX_DATA_POINTS,
  }
}


