import { TOGGLE_LAYER } from '../actions';

const initialState =  {
  layers:[
    {id:'layerSolar',name:"Solar",on:true},
    {id:'layerUsage',name:"Usage",on:true},
    {id:'layerBattery',name:"Battery",on:true}
  ]
};

export default function toggleLayer(state = initialState, action) {
  switch(action.type) {
    case TOGGLE_LAYER:
      return {
        ...state,
        layers: state.layers.map( (layer,index) => {
          if(action.index === index){
            return {id:layer.id,name:layer.name,on:!layer.on}
          } else {
            return layer
          }
          
        })
      };
    default:
      return state;
  }
}


