import { UPDATE_STATUS_TICK } from '../actions';
import { UPDATE_STATUS_VALUE } from '../actions';
import { UPDATE_STATUS_MAX_DATA_POINTS } from '../actions';

const initialState =  {
  tickCount:0,
  dataPointWidth:3,
  maxDataPoints:10,
  maxValue:100,
  valueSolar:80,
  valueUsage:50,
  valueBattery:20,
  statusHistory:[{date:new Date(), solar:80, usage:50, battery:20}]
};

export default function updateStatus(state = initialState, action) {
  switch(action.type) {
    case UPDATE_STATUS_TICK:
      let newBattery = (state.valueBattery + (.01 * (state.valueSolar - state.valueUsage)));
      if(newBattery > 100){
        newBattery = 100;
      } else if(newBattery < 0){
        newBattery = 0;
      }
      return {
        ...state,
        tickCount:state.tickCount++,
        valueBattery:newBattery,
        statusHistory:[{date:new Date(), solar:state.valueSolar, usage:state.valueUsage, battery:newBattery},...state.statusHistory]
      };
    case UPDATE_STATUS_VALUE:
      return {
        ...state,
        ["value"+action.id.slice(11)]:parseInt(action.value,10)
      };
    case UPDATE_STATUS_MAX_DATA_POINTS:
      return {
        ...state,
        maxDataPoints:Math.floor(document.getElementById("TimelineData").offsetWidth / state.dataPointWidth),
      };
    default:
      return state;
  }
}


