import { SET_SCALE } from '../actions';

const initialState =  {
  scales:[1,6,12,24],
	currentScale:24,
};

export default function setScale(state = initialState, action) {
  switch(action.type) {
    case SET_SCALE:
      return {
        ...state,
        currentScale: action.value
      };
    default:
      return state;
  }
}


