import React, { Component } from 'react';
import '../styles/TimelineOptions.css';

class TimelineOptions extends Component {
	onScaleChange = (event) => {
		this.props.onScaleChange(event.target.value);
	}
	onToggleLayer = (event) => {
		this.props.onToggleLayer(parseInt(event.target.attributes.index.value,10));
	}
	onValueChange = (event) => {
		this.props.onValueChange(event.target.id, event.target.value);
	}
	render() {
		const scales = this.props.scale.scales.map( (scale) =>
			<option key={scale} value={scale}>{scale} Hour</option>
		);
		const layers = this.props.layers.map( (layer,index) =>
			<li key={index} id={layer.id+"-parent"} className={this.props.layers[index].on ? 'active': null} >
				<input type='checkbox' onChange={this.onToggleLayer} id={layer.id} index={index} checked={!!this.props.layers[index].on} />
				<label htmlFor={layer.id}>{layer.name}</label>
				<input type="range" min="0" max={this.props.status.maxValue} value={this.props.status["value"+layer.name]} className="valueRange" id={'range-'+layer.id} onChange={this.onValueChange} />
			</li>
		);
		return (
			<div className="TimelineOptions">
				<div className="TimelineOptionsInner">
					<ul>
						{/* TODO: implement scale view rendering
							DONE: scale value onchange to redux
						<li>
							<label htmlFor='scaleSelect'>Scale: </label>
							<select id='scaleSelect' onChange={this.onScaleChange} value={this.props.scale.currentScale}>
								{scales}
							</select>
						</li>*/}
						{layers}
					</ul>
				</div>
			</div>
		);
	}
}

export default TimelineOptions;


