import React, { Component } from 'react';
import '../styles/TimelineView.css';

class TimelineView extends Component {
  render() {
    const timelineData = this.props.status.statusHistory.slice(0, this.props.status.maxDataPoints).map( (history,index) =>
      <div key={index} className="dataContainer">
        <div className='dataPoint bg-surplus'
          style={{
            bottom:0,
            height:history.solar,
            display:(history.solar > 0 ? 'block' : 'none')
        }}>
        </div>
        <div className={'dataPoint bg-usage border-usage'+ (this.props.layers[1].on ? '': ' off')} 
          style={{
            bottom:0,
            height:(history.usage + 2)
          }}>
        </div>
        <div className='dataPoint bg-deficit'
          style={{
            bottom:(history.solar + 1),
            height:(history.usage - history.solar - 1),
            display:((history.solar - 1) < history.usage ? 'block' : 'none')
        }}>
        </div>
        <div className={'dataPoint bg-solar'+ (this.props.layers[0].on ? '': ' off')} 
          style={{
            bottom:history.solar
          }}>
        </div>
        <div className={'dataPoint bg-battery'+ (this.props.layers[2].on ? '': ' off')} 
          style={{
            bottom:history.battery
          }}>
        </div>
      </div>
    );
    return (
      <div id="TimelineView">
        <div id="TimelineData">{timelineData}</div>
        <div id="TimelineBaseline"></div>
        <div id="TimelineStatusContainer">{this.props.status.statusHistory[0].date.toString()}</div>
			</div>
    );
  }
}

export default TimelineView;


