import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import * as Actions from '../actions';
import TimelineOptions from '../components/TimelineOptions';
import TimelineView from '../components/TimelineView';

class App extends Component {
  componentDidMount = () => {
    this.tick();
    this.props.actions.updateMaxDataPoints();
    window.addEventListener("resize", this.props.actions.updateMaxDataPoints);
  }
  componentWillUnmount() {
    window.removeEventListener("resize", this.props.actions.updateMaxDataPoints);
  }
  tick = () => {
    this.props.actions.updateStatus();
    setTimeout(this.tick,100);
  }
  render() {
    return (
      <main>
        <TimelineOptions 
          onScaleChange={this.props.actions.setScale} 
          scale={this.props.scale} 
          layers={this.props.layers.layers} 
          onToggleLayer={this.props.actions.toggleLayer}
          onValueChange={this.props.actions.statusValueChange}
          status={this.props.status} />
        <TimelineView 
          status={this.props.status}
          layers={this.props.layers.layers}  />
      </main>
    );
  }
}

function mapStateToProps(state) {
  return {
    scale: state.scale,
    layers: state.layers,
    status: state.status
  };
}

function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators(Actions, dispatch)
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(App);


